<template>
    <div class="flex flex-col">
        <div class="flex sm:flex-col md:flex-row lg:flex-row xl:flex-row mx-5">
            <ul class="">
                <li class="bg-gray-500 text-gray-200 px-3 py-3">Add  Purchaser</li>
                <li>
                    <FormulateForm
                        v-model="purchaser"
                        @submit="submit"
                    >
                    <FormulateInput 
                        type="text"
                        label="First Name"                        
                        name="firstname"
                        validation="required"
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"
                    
                    />
                    <FormulateInput 
                        type="text"
                        label="Last Name"
                        name="lastname"
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"
                    
                    />
                    <hr>
                    <FormulateForm v-if="purchaser.firstname" v-model="purchaser.data.contact">
                        <FormulateInput 
                        type="text"
                        label="Telephone."                        
                        name="tel"                       
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"                    
                        />
                        <FormulateInput 
                        type="text"
                        label="Mobile"                        
                        name="mobile"
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"                    
                        />
                        <FormulateInput 
                        type="text"
                        label="Email"                        
                        name="email"
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"                    
                        />

                    </FormulateForm>
                        
                    <FormulateInput
                        type="submit"
                        label="Add Buyer"
                        class="bg-blue-600 font-semibold text-gray-100 py-3 px-4"
                        
                    />
                    </FormulateForm>
                </li>
            </ul> 
            <ul>
                <li>
                    <FormulateForm
                        v-model="purchaser.data.address"
                        
                    >
                    <FormulateInput 
                        type="text"
                        label="Lot"                        
                        name="lot"
                        validation="required"
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"
                    
                    />
                    <FormulateInput 
                        type="text"
                        label="Street"
                        name="street"
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"
                    
                    />
                    <hr>
                    <FormulateInput
                        type="text"
                        label="Town/Village"
                        name="town"
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"
                        
                    />
                    <FormulateInput
                        type="text"
                        label="City/Parish"
                        name="city_parish"
                        class="bg-gray-200 font-semibold text-gray-700 py-3 px-4"
                        
                    />
                    </FormulateForm>
                </li>
            </ul>
        
        </div>
        
    </div>
</template>
<script>
export default {
    name: 'add-purchaser',
    data () {
        return {
            purchaser: {
                firstname: null,
                lastname:null,
                data:{
                    address:  null,
                    contact: null
                },
            
            }
        }       
    },
    methods: {
        submit() {            
            this.$store.dispatch('addPurchaser', this.purchaser) 
            this.clearForm()          
        },
        clearForm () {
            this.purchaser = {
                firstname: null,
                lastname:null,
                data:{
                    address:  null,
                    contact: null
                },
            
            }
        }
    }
}
</script>