<template>
    <div class="flex flex-col">
        <div class="flex sm:flex-col md:flex-row lg:flex-row xl:flex-row mx-5">
            <ul class="w-1/4">
                <li class="bg-gray-500 text-gray-200 px-3 py-3">Add Tool</li>
                <li>
                    <FormulateForm
                        v-model="tool"
                        @submit="submit"
                    >
                    <FormulateInput 
                        type="text"
                        label="Tool Name"                        
                        name="name"
                        validation="required"
                        class="bg-blue-600 font-semibold text-gray-700 py-3 px-4"
                    
                    />
                    <FormulateInput 
                        type="text"
                        label="Brand Name"
                        name="brand"
                        class="bg-blue-600 font-semibold text-gray-700 py-3 px-4"
                    
                    />
                    <FormulateInput 
                        type="text"
                        label="Model Number"
                        name="model"
                        class="bg-blue-600 font-semibold text-gray-700 py-3 px-4"
                    
                    />
                    <FormulateInput 
                        type="text"
                        label="Serial Number"
                        name="serial"
                        class="bg-blue-600 font-semibold text-gray-700 py-3 px-4"
                    
                    />
                    <FormulateInput 
                        type="radio"
                        label="Power Supply"
                        name="type"
                        
                        :options="{ power:'Power', battery: 'Battery', fuel:'Fuel'}"
                        class="bg-blue-600 font-semibold text-gray-700 py-3 px-4"
                    />
                    <hr>
                    <FormulateInput
                        type="submit"
                        label="Add Tool"
                        class="bg-blue-600 font-semibold text-gray-100 py-3 px-4"
                        
                    />
                    </FormulateForm>
                </li>
            </ul>
            <ul class="w-1/4">
                <li class="bg-gray-500 text-gray-200 px-3 py-3" v-if="tool.type">Tool Specifications</li>
                 <li class="bg-gray-300" v-if="tool.type =='battery'">
                    <FormulateForm>
                         <FormulateInput 
                        type="number"
                        label="Battery Voltage  V"
                        v-model.number="tool.specification.power.volts"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="number"
                        label="Current  MAh"
                        v-model.number="tool.specification.power.current"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="number"
                        label=" Batteries  Ea."
                        v-model.number="tool.specification.power.amount"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                    </FormulateForm>
                    <FormulateForm>
                        <FormulateInput
                        type="textarea"
                        label="Special Details."
                        v-model="toolprops"
                        class="bg-gray-200 text-gray-800 py-2 px-2"

                    ></FormulateInput>
                    </FormulateForm>
                </li>
                 <li class="bg-gray-300" v-if="tool.type =='fuel'">
                    <FormulateForm
                    
                    >
                         <FormulateInput 
                        type="text"
                        label="Fuel Type Gas89 Gas90 E10 E13 Deisel"
                        v-model="tool.specification.power.fuel"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="number"
                        label="Fuel Capacity  l  ml"
                        v-model.number="tool.specification.power.fuel_capacity"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="text"
                        label="Lubricant  Oil-10 Oil-20 Oil-30 Oil-40"
                        v-model="tool.specification.power.lubricant"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                    </FormulateForm>
                    <FormulateForm>
                        <FormulateInput
                        type="textarea"
                        label="Special Details."
                        v-model="toolprops"
                        class="bg-gray-200 text-gray-800 py-2 px-2"

                    ></FormulateInput>
                    </FormulateForm>

                </li>
                <li class="bg-gray-300" v-if="tool.type =='power'">
                    <FormulateForm>                   
                         <FormulateInput 
                        type="number"
                        label="Voltage  V"
                        v-model.number="tool.specification.power.volts"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     
                     <FormulateInput 
                        type="number"
                        label="Current Rating Ah"
                         v-model.number="tool.specification.power.current"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="number"
                        label="Voltage Cycle  Hz"
                        v-model.number="tool.specification.power.cycles"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                    </FormulateForm>
                    <FormulateForm>
                        <FormulateInput
                        type="textarea"
                        label="Special Details."
                        v-model="toolprops"
                        class="bg-gray-200 text-gray-800 py-2 px-2"

                    ></FormulateInput>
                    </FormulateForm>

                </li>
                <li>

                   
                </li>
            </ul>
                        <ul class="w-1/4 " v-if="tool.type">
                <li class="bg-gray-500 text-gray-200 px-3 py-3">Supplier and Stocking Details</li>
                 <li class="bg-gray-300">
                    <FormulateForm>
                         <FormulateInput 
                        type="text"
                        label="Supplier."
                        v-model="tool.data.supplier"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="text"
                        label="Category"
                        v-model="tool.data.category"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="number"
                        label="Stock Quantity"
                        v-model.number="tool.data.in_stock"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                    </FormulateForm>
                    
                </li>
                <li v-if="tool.type">
                    <FormulateForm>
                         <FormulateInput 
                        type="number"
                        label="Supply Price USD"                        
                        v-model.number="tool.price.supplier.usd"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="number"
                        label="Supply Price JAD"
                         v-model.number="tool.price.supplier.jad"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                    </FormulateForm>
                </li>
                 <li v-if="tool.type">
                    <FormulateForm>
                         <FormulateInput 
                        type="number"
                        label="Sale Price USD"
                        v-model.number="tool.price.sale.usd"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                     <FormulateInput 
                        type="number"
                        label="Sale Price JAD"
                        v-model.number="tool.price.sale.jad"
                        class="bg-gray-300 font-normal text-gray-800 py-1 px-2"
                    
                    />
                    </FormulateForm>
                </li>
            </ul>
           
        
        </div>
        <p class="text-xs bg-gray-200">{{tool}}</p>
    </div>
</template>
<script>
export default {
    name: 'add-tool',
    data () {
        return {
            toolprops: null,
            tool: {
                name: null,
                brand:null,
                type:null,
                specification:{
                    power:{},
                    props:[]
                },
                data:{
                    supplier: null
                },
                price: {
                    supplier: {},
                    sale: {}
                }
            }
        }       
    },
    methods: {
        submit() {
            if(this.toolprops) {
                this.tool.specification.props = this.toolprops.split(',')
            }
            this.$store.dispatch('addTool', this.tool)
            
        }
    }
}
</script>