<template>
    <div class="tool-index">
        Tools Index
        <ul v-if="allTools">
            <li v-for="tool of allTools" :key="tool.id">
            {{tool.id}} {{tool.name}} {{tool.brand}}
            <span v-for="img of tool.images" :key="img"><img class="h-20" :src="url + tool.image_url + img" ></span>
            
            </li>
        </ul>
    </div>
</template>
<script>
import { mapGetters } from 'vuex';
export default {
    name: "tool-index",
    data () {return {
        url: "http://" + location.hostname +":7707"
    }},
    computed: {
    ...mapGetters({
      allTools: 'allTools',
      errorEvent: 'errorEvent'
      
        })
    }

}
</script>